@-webkit-keyframes blinker {
    50% {
        opacity: 0.7;
    }
}

@keyframes blinker {
    50% {
        opacity: 0.7;
    }
}

@-webkit-keyframes App-logo-spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    25% {
        -webkit-transform: rotate(4deg);
                transform: rotate(4deg);
    }
    50% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    75% {
        -webkit-transform: rotate(-4deg);
                transform: rotate(-4deg);
    }
    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@keyframes App-logo-spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    25% {
        -webkit-transform: rotate(4deg);
                transform: rotate(4deg);
    }
    50% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    75% {
        -webkit-transform: rotate(-4deg);
                transform: rotate(-4deg);
    }
    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

.speccy_flash_black {
    -webkit-animation: SPECCY_FLASH_BLACK 0.7s infinite;
            animation: SPECCY_FLASH_BLACK 0.7s infinite;
    padding: 0.4vh 0.8vw 0.1vh 0.8vw;
    transition-duration: 0s;
}

@-webkit-keyframes SPECCY_FLASH_BLACK {
    0%, 49% {
        background-color: rgb(0, 0, 0);
        color: rgb(189, 189, 189);
        -webkit-animation-timing-function: steps(1, end);
                animation-timing-function: steps(1, end);
    }
    50%, 100% {
        background-color: rgb(189, 189, 189);
        color: rgb(0, 0, 0);
        -webkit-animation-timing-function: steps(1, end);
                animation-timing-function: steps(1, end);
    }
}

@-webkit-keyframes SPECCY_FLASH_GREEN {
    0%, 49% {
        background-color: rgb(0, 255, 0);
        color: rgb(0, 00, 0);
        -webkit-animation-timing-function: steps(1, end);
                animation-timing-function: steps(1, end);
    }
    50%, 100% {
        background-color: rgb(0, 128, 0);
        color: rgb(255, 255, 255);
        -webkit-animation-timing-function: steps(1, end);
                animation-timing-function: steps(1, end);
    }
}

@-webkit-keyframes SPECCY_FLASH_RED {
    0%, 49% {
        background-color: rgb(255, 0, 0);
        color: rgb(0, 00, 0);
        -webkit-animation-timing-function: steps(1, end);
                animation-timing-function: steps(1, end);
    }
    50%, 100% {
        background-color: rgb(128, 0, 0);
        color: rgb(255, 255, 255);
        -webkit-animation-timing-function: steps(1, end);
                animation-timing-function: steps(1, end);
    }
}

@-webkit-keyframes SPECCY_FLASH_TAPE_LOAD {
    0%, 60% {
        border-color: #bf0000;
        -webkit-animation-timing-function: steps(1, end);
                animation-timing-function: steps(1, end);
    }
    60%, 80% {
        border-color: #00bdbe;
        -webkit-animation-timing-function: steps(1, end);
                animation-timing-function: steps(1, end);
    }
    80%, 100% {
        border-color: rgb(189, 189, 189);
        -webkit-animation-timing-function: steps(1, end);
                animation-timing-function: steps(1, end);
    }
}

.LeftStringSpin {
